import { ChangeDetectorRef, Component, OnDestroy, ViewChild } from '@angular/core';
import { FileService } from 'app/services/file';
import { I360Validators } from 'app/utils/validator/validators';
import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { getImagePath } from 'app/core/misc';
import { MAX_FILE_SIZE } from 'app/utils/file-uploader/file-uploader.component';
import { SupportService } from 'app/services/support';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { switchMap, tap, firstValueFrom } from 'rxjs';
import { Panel } from 'app/utils/panel';


@Component({
    selector: 'support',
    styleUrls: ['../settings/settings-index.component.scss', 'support.component.scss'],
    templateUrl: 'support.component.html',
    providers: [SupportService],
})
export class SupportComponent implements OnDestroy {
    public form = this.formBuilder.group({
        email: this.formBuilder.control('',
            [I360Validators.required('email'), I360Validators.email]),
        subject: this.formBuilder.control('', I360Validators.required('subject')),
        description: this.formBuilder.control('', I360Validators.required('description')),
        cln: this.formBuilder.control(''),
        file: this.formBuilder.control([],
            [I360Validators.fileSize(MAX_FILE_SIZE), I360Validators.maxFiles(5)]),
    });
    public ticketUrl;
    public submitted = false;
    public imagePath: string = getImagePath();
    @ViewChild(LoaderDirective, {static: false}) public loader: LoaderDirective;

    public constructor(
        private formBuilder: I360FormBuilder,
        private fileService: FileService,
        private supportService: SupportService,
        public panel: Panel,
        public detector: ChangeDetectorRef,
    ) {}

    @useLoader
    async submit() {
        this.submitted = true;
        const {email, subject, description, cln, file} = this.form.value;
        await firstValueFrom(this.fileService.upload(file).pipe(
            switchMap((uploaded) => {
                return this.supportService.send({
                    email,
                    subject,
                    description,
                    cln,
                    ...(
                        uploaded ? {attachments: [uploaded.data]} : {}
                    ),
                });
            }),
            tap(response => {
                this.ticketUrl = response.data.items.pop();
                this.form.reset();
                this.detector.detectChanges();
            }),
        ), { defaultValue: undefined });
        this.submitted = false;
    }

    ngOnDestroy(): void {
        this.ticketUrl = '';
    }
}
